.talks-list {
  padding: 0;
  .col-md-6:nth-child(even){
    padding-left: .15rem;
  }
  .col-md-6:nth-child(odd){
    padding-right: .15rem;
  }
  .lead{
    font-weight: 400;
    color: black;
    background-color: whitesmoke;
  }
  img{
    height: 24px;
    margin-right: .5rem;
    width: auto;
    border-radius: 50%;
    background: white;

  }
  .img-badge{
    border-radius: 0;
    max-width: 100%;
    height: auto;
  }
	li {
    cursor: pointer;
    margin-bottom: .3rem;
    color: #3b3b3b;
    list-style: none;
		background: rgba(0, 0, 0, 0.05);
		transition: all 0.1s ease;
		padding: 1rem !important;
		align-items: center;
		i {
			width: 30px;
			text-align: center;
			padding-right: 0.5rem;
		}
		&:hover {
			background: rgba(0, 0, 0, 0.1);
		}
		.badge {
			box-shadow: none;
		}
  }
  .thumbnail iframe {
    width: 700px;
    height: 500px;
    pointer-events: none;
  }
  .thumbnail {
    zoom: 0.0;
    transform: scale(0.25);
    transform-origin: 0.0;
    -ms-zoom: 0.25;
    -moz-transform: scale(0.25);
    -moz-transform-origin: 0 0;
    -o-transform: scale(0.25);
    -o-transform-origin: 0 0;
    -webkit-transform: scale(0.25);
    -webkit-transform-origin: 0 0;
  }
  .thumbnail-container {
    width: calc(700px * 0.25);
    height: calc(500px * 0.25);
    display: inline-block;
    overflow: hidden;
  }
  
}


